import { inject } from 'vue'
import config from '@/data/config';

export default {

    init() {
        const axios = inject('axios');
        if (axios !== undefined) {
            axios.defaults.headers.post['Content-Type'] ='application/x-www-form-urlencoded';
            axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
            axios.defaults.withCredentials = false;
            axios.defaults.headers.common['Access-Control-Allow-Methods'] = '*';
            axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Origin, Methods, Content-Type, Authorization';
            axios.defaults.headers.common['Access-Control-Allow-Credentials'] = true;

            axios.defaults.headers.common['Authorization'] = `Bearer ${process.env.NOTION_API_KEY}`;
            axios.defaults.headers.common['Notion-Version'] = '2022-02-22';
            axios.defaults.baseURL = `${config.NOTION_PROXY}/${config.NOTION_ENDPOINT_DB}`;
        }
        return axios;
    },

    get(resource, slug = "") {
        return new Promise((resolve) => {
            const axios = inject('axios');
            this.init(axios);
            axios.get(`${resource}/${slug}`).then(res => resolve(res));
        });
    },

    post(resource, params) {
        return new Promise((resolve) => {
            const axios = this.init();
            console.log('post.path:', `$${resource}`);
            axios.post(`${resource}`, params).then(res => resolve(res)).catch((error) => resolve(error));
        });
    },

    update(resource, slug, params) {
        const axios = inject('axios');
        return new Promise((resolve) => {
            this.init(axios);
            axios.put(`${resource}/${slug}`, params).then(res => resolve(res));
        });
    },

    put(resource, params) {
        return new Promise((resolve) => {
            const axios = inject('axios');
            this.init(axios);
            axios.put(`${resource}`, params).then(res => resolve(res));
        });
    },

    delete(resource) {
        return new Promise((resolve) => {
            const axios = inject('axios');
            this.init(axios);
            axios.delete(`${resource}`).then(res => resolve(res));
        });
    }
};